<template>
  <div>
    <el-dialog :visible.sync="visible" :before-close="beforeClose" :append-to-body="true" title="新增人员" width="800px">
      <div class="p15">
        <el-form :show-message=true :inline-message=true ref="form" :model="form" :rules="rules" class="form-text f13 " label-width="108px" size="mini">
          <el-row>
            <el-col :span="16">
              <el-form-item label="发布标题：" prop="title">
                <el-input
                  placeholder='请输入标题'
                  v-model='form.title'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="22">
              <el-form-item label="公告内容：" prop="content">
                <el-input type="textarea" :rows="6" v-model='form.content'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button type="primary" @click="closeDialog('save')">保存草稿</el-button>
        <el-button type="primary" @click="closeDialog('publish')">保存发布</el-button>
        <el-button @click="closeDialog('close')">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'Dialog',
    props: ['dialogFormVisible'],
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      }
    },
    data() {
      return {
        showModal: false,
        form: {
          title: '',
          content: ''
        },
        rules: {
          title: [
            { required: true },
          ],
          content: [
            { required: true },
          ]
        }
      }
    },
    methods: {
      closeDialog(method) {
        this.$emit('closeDialog', this.form, method)
        if (method === 'publish') {
          this.form = {
            title: '',
            content: ''
          }
          this.$refs['form'].resetFields()
        }
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog', {}, 'close')
      }
    }
  }
</script>
<style lang="scss">
  .dialog_input_custom{
    >input{
      height: 28px;
      font-size: 12px;
      top: 1px;
      position: relative;
    }
  }
  .dialog_date_custom{
    .el-input__prefix{
      top: -6px;
    }
  }
</style>


